import bind from 'delegate'
import popin from 'modules/popin'

let binded = false

function addressIsValid(e) {
  const currentPopin = e.target.closest('.popin-wrapper')

  if (!currentPopin) {
    return
  }

  const validCallback = currentPopin.data('validCallback')

  if (!validCallback) {
    return
  }

  validCallback()
}

function display(params) {
  // register an event handler  if not already registered
  if (!binded) {
    bind(document.body, '.popin [data-is-valid="true"]', 'click', addressIsValid)
    binded = true
  }

  // display the popin
  popin.open(params)
    .then(data => {
      const modal = data.modal
      modal.el.data('validCallback', params.validCallback)
      modal.el.setAttribute('role', 'dialog')
      modal.el.removeAttribute('aria-describedby')
    })
}

export default { display }
