import config from 'lib/config'
import { select, selectAll, getCollection } from 'lib/selectors'

function hilightCreditCard(e) {
  let isValidCardSupport
  const creditCardInput = e.delegateTarget
  const form = creditCardInput.closest('form')
  const submitBtn = select('button[type="submit"]', form)
  const inputField = creditCardInput.closest('.field')
  const cardIconList = select('.js-bank-icons')
  const role = select('[aria-live="assertive"]', form)
  const cardSupportErr = select('.js-cardNumber-error')
  const cardCVNTootip = select('#tooltip-card_cvn')
  const amexTooltip = select('.amex-tooltip')
  const otherTooltip = select('.other-tooltip')
  
  
  let creditCardInputFourDigit

  if (creditCardInput) {
    creditCardInputFourDigit = creditCardInput.value.split('', 4).join('')
  }

  if (creditCardInputFourDigit.length === 4) {
    const dataRegex = selectAll('.js-bank-icons [data-regex]')
    const regexList = dataRegex.filter(reg => reg.dataset.regex.length > 0)

    const cardTypeEl = select('#selectCreditCardType')
    if (cardTypeEl) {
      cardTypeEl.removeAttribute('data-cardIcon-value')
    }
    getCollection('.supported-card').forEach(card => {
      card.classList.remove('supported-card')
    })
    regexList.forEach(regex => {
      const fourDigitRegex = regex.dataset.regex.replace(RegExp(/{[0-9]}|{[0-9][0-9]}{1,}/g), '{0,}')

      if (RegExp(fourDigitRegex).test(creditCardInputFourDigit)) {
        highlightIcon(regex.id)
      }
    })
    handleErr()
  } else {
    undoHighlight()
  }

  function highlightIcon(regex) {
    let cardName
    let cardNameId
    const supportedCard = select(`li#${regex} .bank-icon`)
    const cardTypeEl = select('#selectCreditCardType')

    if (select(`li#${regex}`)) {
      cardName = select(`li#${regex}`).dataset.cardName
      cardNameId = select(`li#${regex}`).getAttribute('id')
    }
    if (cardIconList && supportedCard) {
      if (cardCVNTootip) {
    	const tooltipBtn = select('button', cardCVNTootip) 
    	if (tooltipBtn) {
          if (supportedCard.classList.contains('is-American')) {
    	    tooltipBtn.setAttribute('data-tooltip', amexTooltip.innerHTML)          
    	  }
          else {
            tooltipBtn.setAttribute('data-tooltip', otherTooltip.innerHTML)
          }
        }
      }
      cardIconList.classList.add('bank-icons--active')
      supportedCard.classList.add('supported-card')
      if (cardTypeEl && cardNameId)
        if (cardTypeEl.getAttribute('data-cardIcon-value')) {
          cardTypeEl.setAttribute('data-cardIcon-value', cardTypeEl.getAttribute('data-cardIcon-value') + "|" + cardNameId)
        } else {
          cardTypeEl.setAttribute('data-cardIcon-value', cardNameId)
        }
    }
    if (role && cardName && !role.innerText) {
      const roleMsg = document.createTextNode(`${config.dl.opc.paymentMethod.selected} ${cardName}`)
      role.appendChild(roleMsg)
    }
  }

  function undoHighlight() {
    if (cardIconList) {
      cardIconList.classList.remove('bank-icons--active')
    }
    selectAll('.supported-card').forEach(card => {
      card.classList.remove('supported-card')
    })
    if (role) {
      role.innerText = ''
    }
  }

  function handleErr() {
    isValidCardSupport = selectAll('.js-bank-icons .bank-icon').some(icon => icon.classList.contains('supported-card'))
    // eslint-disable-next-line
    !isValidCardSupport ? addErr() : removeErr()
  }

  function addErr() {
    if (cardSupportErr) {
      cardSupportErr.innerText = config.locales.errors.form.global.paymentMethodes.notAccepted
      cardSupportErr.removeAttribute('role')
    }
    if (inputField) {
      inputField.classList.add('has-error')
    }
    if (submitBtn) {
      submitBtn.setAttribute('aria-disabled', true)
      submitBtn.addEventListener('click', handleSubmitOrder)
    }
  }

  function removeErr() {
    if (cardSupportErr) {
      cardSupportErr.innerText = ''
      cardSupportErr.setAttribute('role', 'presentation')
    }
    if (inputField) {
      inputField.classList.remove('has-error')
    }
    if (creditCardInput && cardSupportErr) {
      creditCardInput.setAttribute('aria-describedby', cardSupportErr.id)
    }
    if (submitBtn) {
      submitBtn.setAttribute('aria-disabled', false)
      submitBtn.removeEventListener('click', handleSubmitOrder)
    }
  }
}

function handleSubmitOrder(e) {
  e.preventDefault()
  const creditCardInput = select('[name="card_number"]')

  if (creditCardInput) {
    creditCardInput.focus()
  }
}

export default hilightCreditCard
