import {select, selectAll} from 'lib/selectors'
import {focusElement, isWideScreen, tabIndexToZero} from 'lib/helpers'
import popin from 'modules/popin'
import config from 'lib/config'

// Selectors
const DIALOG_CLOSE_BUTTON_SELECTOR = 'div.yes-no-modal button.popin__close.js-popin-close'

let submitted = false;

class ThreeDS2Popin {
  constructor(el) {
    this.el = el
    this.buttonClose = null
    this.params = {
    }
  }

  async init() {
    let challengeTokenIdElem = select("#challengeTokenId")
    let challengeToken = challengeTokenIdElem.value
    let legacy = challengeTokenIdElem.dataset['legacy']
    let checkGiftCard = select('#isGiftCard')
    let isGiftCard = checkGiftCard.value == 'true' ? true : false
    this.buildTemplate(challengeToken, legacy, isGiftCard)
}

  perform3DS2Operations (tokenID) {
    const isChallengeShopper = select("#challengeShopperId").value
    if (isChallengeShopper) {
      this.initiate3DS2ChallengeShopper(tokenID);
    }
  }

  initiate3DS2ChallengeShopper(challengeToken) {
    submitted = false;
    var threeDS2ChallengeShopperNode = select('#threeDS2');
    const localeValue = select("#localId").value
    const envValue = select("#envId").value
    var configuration = {
      locale: localeValue,
      environment: envValue
    }
    const checkout = new AdyenCheckout(configuration);
    var challengeShopperComponent = checkout
      .create( 'threeDS2Challenge', {
        challengeToken: challengeToken,
        onComplete: function ( challengeData ) {
          if (submitted) {
            // Already submitted. Ignore this call
            return;
          }
          submitted = true;
          this.challengeResult = challengeData.data.details["threeds2.challengeResult"];
          var challengeResultField = select("#challengeResult");
          challengeResultField.value = this.challengeResult;
          var threeDS2popinCol = select('.popin__col')
          threeDS2popinCol.setAttribute('style', 'visibility:hidden');
          document.getElementById("3ds2-form").submit();
        },
        onError: function (error) {
          console.error(JSON.stringify(error));
        },
        size: '05'
      } );
    challengeShopperComponent.mount(threeDS2ChallengeShopperNode);
  }

  buildTemplate(tokenID, legacy, isGiftCard) {
    this.template =
      `<div class="popin-text text-center" id="threeDS2-popin-wrapper" data-close-label="${window.config.locales.opc.threeDS2Auth.closethreeDS2Auth}" data-test="secThreeDS2Popin">
        <div class="modal-body">
          <div id="threeDS2" class="heading is-5" role="heading"></div>
           <form method="post" id="3ds2-form" action="${legacy ? window.config.webservices.checkout.threeDS2ResponseLegacy :(isGiftCard ? window.config.webservices.checkout.threeDS2GiftCard : window.config.webservices.checkout.threeDS2Response)}">
                <input type="hidden" name="challengeToken" value="${tokenID}" />
                <input type="hidden" id="challengeResult" name="challengeResult" />
          </form>      
        </div>
      </div>`
  }

  onOpenPopin() {
    let challengeToken = select("#challengeTokenId").value
    this.perform3DS2Operations(challengeToken)
    this.setDomElements()
  }

  setDomElements() {
    this.popinEl = select('.threeDS2popin')
    if (this.popinEl) {
      this.popinEl.removeAttribute('aria-labelledby')
      this.popinEl.setAttribute('aria-label', config.popin3DS2.authLabelTitle)
      this.popinEl.setAttribute('tabindex', '-1');
    }

    this.buttonClose = select('.js-popin-close', this.popinEl)
    this.form = select('form', this.popinEl)
  }

  /**
   * display a yes(remove) or no(cancel) dialog
   * @param data {Object}: contains additional params
   */
   open() {
    popin.open({
      html: this.template,
      role: 'dialog',
      modifiers: 'is-relative threeDS2popin',
      opener: null,
      manageFocus: true,
      forceInert: true,
      modal: true,
      action: 'removeCancelModal',
      outerClose: false
    }).then(() => {
      this.onOpenPopin()
    })
    // To catch the esc key
    window.addEventListener('keydown', this.onEscapeKeyClicked)
  }

  /**
   * Close the active modal
   */
  closeModal() {
    window.removeEventListener('keydown', this.onEscapeKeyClicked)
    popin.hide()
  }

  /**
   * Triggered once user clicked on the modal cancel button
   * @param params {MouseEvent}: contains MouseEvent parameters
   */
  onModalCancelButtonClicked = (e) => {
    this.closeModal()
    focusElement(this.params.clickedElm)
    if (!isWideScreen()) {
      const links = selectAll('.collapse-panel__header .js-collapse-button')
      if (links.length > 0) {
        tabIndexToZero(links)
      }
    }
  }

  /**
   * Triggered once user clicked on keyboard inputs
   * @param e {MouseEvent}: MouseEvent parameters 
   */
  onEscapeKeyClicked = (e) => {
    if ((e.key === 'Escape' || e.key === 'Esc' || e.keyCode === 27)) {
      this.onModalCancelButtonClicked(e)
    }
  }

}

const initThreeDS2Popin = () => {
  const threeDS2 = new ThreeDS2Popin()
  threeDS2.init().then(() => {
    threeDS2.open()
  })
}

export default initThreeDS2Popin