import {select, selectAll} from 'lib/selectors'
import {focusElement, isWideScreen, tabIndexToZero} from 'lib/helpers'
import popin from 'modules/popin'
import config from 'lib/config'
import * as xhr from 'modules/fetch'

// Selectors
const DIALOG_CLOSE_BUTTON_SELECTOR = 'div.yes-no-modal button.popin__close.js-popin-close'



class ChangeCountry {
  constructor(el) {
    this.el = el
    this.buttonClose = null
    this.params = {
    }

  }

  async init() {
    let responseData = await xhr.get(config.markets.checkoutUrl)

    if (responseData && responseData.markets) {
      const markets = responseData.markets
      responseData.sortedMarketKeys = Object.keys(markets).sort((key1, key2) => {
        let val1 = markets[key1].label.toUpperCase()
        let val2 = markets[key2].label.toUpperCase()

        if (val1 == val2) {
          return 0
        } else if (val1 > val2) {
          return 1
        } else {
          return -1
        }
      })
    }

    this.data = responseData
    this.buildTemplate()
  }

  // templates
  buildTemplate() {
    this.template =
      `<div class="popin-text text-center" data-close-label="${window.config.locales.opc.countryChange.closeChangeDeliveryCountry}" data-test="secChangeDeliveryCountryPopin">
        <div class="modal-body">
          <form id="changeDeliveryCountryForm" novalidate="true" method="" action="">
            <p id="title-change-delivery-country" class="heading is-5" role="heading" aria-level="1">
              ${window.config.locales.opc.countryChange.deliveryCountry}
            </p>
            <p class="sub-heading">${window.config.locales.opc.countryChange.currentlyOnlineBoutique.replace('{0}', '<strong>' + this.data.current.name + '</strong>')} </p>
            <p>${window.config.locales.opc.countryChange.changeLocationDescription}</p>
            <div class="field" data-id="">  
              <div class="js-label-input has-select is-ready">
              <label class='heading is-7 text-center title-language' for="market-selection" data-test="lblChangeYourLocation">${window.config.locales.opc.countryChange.changeYourLocation}</label>
              `+ this.marketSelectTemplate() + `
              </div>
            </div>
            <p class="product-message">${window.config.locales.opc.countryChange.cartMessage}</p>
            <div class="button__wrapper">
                  <button type="submit" class="button is-primary validate" data-test="btnLanguageSubmit">
                    <span class="button__wrapper">
                      <span class="label">${window.config.locales.opc.countryChange.submit}</span>
                    </span>
                  </button>
            </div>
          </form>
        </div>
      </div>`
  }

  createSelectBox() {
    const el = document.createElement('select')
    el.classList.add('selectbox__input')
    el.classList.add('is-simple')
    el.classList.add('select-country-focus')
    el.setAttribute('data-test', 'drdwnChangeCountry')
    el.setAttribute('required', 'true')
    return el
  }

  marketSelectTemplate() {
    const el = this.createSelectBox()
    el.id = "market-selection"
    this.populateMarkets(el, true)
    const html = this.setNiceSelectTemplate(el)
    return html
  }

  populateMarkets(marketSelection, firstLoad = false) {
    // remove initial content
    while (marketSelection.firstChild) {
      marketSelection.removeChild(marketSelection.firstChild)
    }

    let markets = this.data.markets

    //Default select country option
    var option = document.createElement('option')
    option.innerText = `${window.config.locales.opc.countryChange.defaultOption}`
    option.value = ""
    option.setAttribute('selected', 'selected')
    marketSelection.append(option)
    if(this.data.sortedMarketKeys) {
      this.data.sortedMarketKeys.forEach((marketKey) => {
        var option = document.createElement('option')
        option.innerText = markets[marketKey].label
        option.value = marketKey
        marketSelection.append(option)
      })
    }
  }
  setNiceSelectTemplate(el) {
    const html = `<span class="selectbox is-simple is-ready has-content i18n is-using-keyboard">
                  `+ el.outerHTML + `
                  <span class="selectbox__wrapper" aria-hidden="true">
                    <span></span>
                  </span>
                  <svg data-test="" class="icon is-small  is-dropdown" focusable="false" viewBox="0 0 1 1" aria-hidden="true">
                    <use xlink:href="${config.paths.icons}icons.svg${config.targetVersion}#dropdown"></use>
                  </svg>
                </span>`
    return html
  }
  onOpenPopin() {
    this.setDomElements()
  }

  setDomElements() {
    this.popinEl = select('.changeCountry')
    this.buttonClose = select('.js-popin-close', this.popinEl)
    this.marketSelection = select('#market-selection', this.popinEl)
    this.buttonValidate = select('button.validate', this.popinEl)
    this.form = select('form', this.popinEl)
    this.marketSelection.addEventListener('change', this.onChangeMarket.bind(this))
    this.productMessage = select('.product-message', this.popinEl)
    this.productMessage.setAttribute('hidden','')
    this.buttonValidate.addEventListener('click', this.onValidate.bind(this))
  }

  onValidate(event) {
    if (this.data.markets[this.marketSelection.value].isFallback) {
      event.preventDefault();
      window.location.href = this.data.markets[this.marketSelection.value].url
    }
  }

  onChangeMarket(e) {
    let selectedMarket = e.target.value
    selectedMarket && this.data.markets[selectedMarket].isFallback ? this.productMessage.removeAttribute('hidden') : this.productMessage.setAttribute('hidden', '')
    if(selectedMarket && this.data.markets[selectedMarket].url){
      this.formUrl = this.data.markets[selectedMarket].url
      this.form.setAttribute("method", "POST")
      this.form.setAttribute("action", this.formUrl)
    }   
  }

  /**
   * display a yes(remove) or no(cancel) dialog
   * @param data {Object}: contains additional params
   */
  open() {
    popin.open({
      html: this.template,
      role: 'dialog',
      modifiers: 'is-relative changeCountry',
      opener: null,
      manageFocus: true,
      forceInert: true,
      modal: true,
      action: 'removeCancelModal',
      outerClose: false
    }).then(() => {
      this.onOpenPopin()
    })
    // To catch the esc key
    window.addEventListener('keydown', this.onEscapeKeyClicked)
  }

  /**
   * Close the active modal
   */
  closeModal() {
    window.removeEventListener('keydown', this.onEscapeKeyClicked)
    popin.hide()
  }

  /**
   * Triggered once user clicked on the modal cancel button
   * @param params {MouseEvent}: contains MouseEvent parameters
   */
  onModalCancelButtonClicked = (e) => {
    this.closeModal()
    focusElement(this.params.clickedElm)
    if (!isWideScreen()) {
      const links = selectAll('.collapse-panel__header .js-collapse-button')
      if (links) {
        tabIndexToZero(links)
      }
    }
  }


  /**
   * Triggered once user clicked on keyboard inputs
   * @param e {MouseEvent}: MouseEvent parameters 
   */
  onEscapeKeyClicked = (e) => {
    if ((e.key === 'Escape' || e.key === 'Esc' || e.keyCode === 27)) {
      this.onModalCancelButtonClicked(e)
    }
  }

}


const initChangeCountry = () => {
  const changeCountry = new ChangeCountry()
  changeCountry.init().then(() => {
    changeCountry.open()
  })
}

export default initChangeCountry