import {select, selectAll, getCollection} from 'lib/selectors'
import config from 'lib/config'
import bind from 'delegate'

/**
 * Triggered once user change newsletter checkbox value
 * @param evt {object}: click event
 */
export default function preferenceSetting(el) {
  
  let heightOfNewsletterRule = 0 
  
  function handlePreferenceSetting(e) {
    const newsletterElm = e.delegateTarget

    if (newsletterElm) {
      const newsletterRulesElm = select('div#newsletter-rules')
      const isChecked = newsletterElm.checked
      
      if(newsletterRulesElm) {
        if (config.country === 'GB' && config.pageName === 'checkout') {
          if (isChecked) {
            if (newsletterRulesElm.hasAttribute('hidden')) {
              newsletterRulesElm.classList.remove('is-hidden')
              newsletterRulesElm.removeAttribute('hidden')
            }
            newsletterRulesElm.classList.add('has-margin-bottom-sm')
          } else {
            newsletterRulesElm.setAttribute('hidden', '')
            newsletterRulesElm.classList.remove('has-margin-bottom-sm')
            newsletterRulesElm.classList.add('is-hidden')
          }
          
        } else {
        // Add/Remove class to avoid margin mistake
          newsletterRulesElm.classList.remove('is-hidden')
          newsletterRulesElm.removeAttribute('hidden')
        } 

        // Used only for first times to get height
        if (!heightOfNewsletterRule) {
          newsletterRulesElm.classList.remove('is-hidden')
          heightOfNewsletterRule = newsletterRulesElm.clientHeight
          newsletterRulesElm.style.height = 0
        }

        // Start animation for both check and unchecked
        TweenLite.fromTo(newsletterRulesElm, (isChecked ? 1 : .4),
          { autoAlpha: isChecked ? 0 : .5, height: isChecked ? 0 : heightOfNewsletterRule },
          { autoAlpha: isChecked ? 1 : .0, height: isChecked ? heightOfNewsletterRule : 0 }
        ).eventCallback('onComplete',() => {
          if (isChecked) {
            newsletterRulesElm.removeAttribute('hidden')
          } else {
            newsletterRulesElm.setAttribute('hidden', '')
          }
        })
      }

      // Stuff for the Newsletter interest
      getCollection('.gigya-status', el).forEach((elm) => {
        elm.value = isChecked ? 'INTERESTED' : 'UNSET'
      })

      const gigyaEmailStatusElm = selectAll('.gigya-status-value', el)
      gigyaEmailStatusElm.forEach(el => {
        el.value = isChecked ? 'OPTIN' : 'UNSET'
      })
        
    }
  }
  
  bind(el, 'input#newsletter-subscribe', 'change', handlePreferenceSetting)
  
}
