/* eslint-disable no-new */
import Base from 'modules/base'
import bind from 'delegate'
import {select, selectAll} from 'lib/selectors'
import {applePayIsSupported} from 'lib/helpers'
import {Manager, OPC_STEP_PROCESS} from 'lib/events'
import {getApplePayData, startApplePaySession} from "../../lib/applepay/applepay-common";

// Selectors
const MODULE_SELECTOR = '[data-page-type=checkout]'
const OPC_APPLE_PAY_INPUT_SELECTOR = '.js-applepay-enable'
const APPLE_PAY_CTA_SELECTOR = '.js-apple-pay-checkout-btn'

class ApplePayOPCManager extends Base {

  constructor(el) {
    super(el)
    this.el = el
    this.params = {
      applePayData: null
    }
    this.bindEvents()
  }

  bindEvents() {
    bind(this.el, `${APPLE_PAY_CTA_SELECTOR}`, 'click', this.onApplePayCheckoutButtonClicked)
    window.addEventListener('load', this.onPageLoad)
    Manager.subscribe(OPC_STEP_PROCESS, this.onOPCStepProcess)
  }

  /**
   * This enable ApplePay radio button if ApplePay is supported
   */
  renderExpressCheckoutCtas() {
    if (applePayIsSupported()) {
      const applePayEl = select(`${OPC_APPLE_PAY_INPUT_SELECTOR}`, this.el)
      if (applePayEl) {
        getApplePayData('checkout').then((data) => {
          this.params.applePayData = data
          applePayEl.removeAttribute('hidden')
        })
      }
    }
  }


  disablePaymentModes() {
    const billingForm = select('form[id="sopPaymentDetailsForm"]', this.el)
    const billingRadioButtonsElm = select('.js-billing-radio-buttons', billingForm)
    const radioButtonsElm = selectAll('li input', billingRadioButtonsElm)

    radioButtonsElm.forEach(radioElm => {
      if (!radioElm.checked) {
        radioElm.disabled = true
      }
    })
  }

  enablePaymentModes() {
    const billingForm = select('form[id="sopPaymentDetailsForm"]', this.el)
    const billingRadioButtonsElm = select('.js-billing-radio-buttons', billingForm)
    const radioButtonsElm = selectAll('li input', billingRadioButtonsElm)

    radioButtonsElm.forEach(radioElm => {
      radioElm.disabled = false
    })
  }

  updateBillingStep() {
    const billingForm = select('form[id="sopPaymentDetailsForm"]', this.el)
    const applePayBlockElm = select('div[id="applePay-method"]', billingForm)
    const placeOrderBlockElm = select('[id="apple-pay-payment"]', billingForm)

    // Hide ApplePayBlock, display order block and disable the unchecked input
    applePayBlockElm.classList.add('is-hidden')
    placeOrderBlockElm.classList.remove('is-hidden')
    this.disablePaymentModes()
    // Set order button action for apple pay
    billingForm.action = window.config.webservices.applepay.placeOrder
  }

  onPageLoad = () => {
    this.renderExpressCheckoutCtas()
  }

  /**
   * Triggered once Each OPC step is activated
   */
  onOPCStepProcess = () => {
    this.renderExpressCheckoutCtas()
  }

  onApplePayCheckoutButtonClicked = (event) => {
    const shippingInfo = JSON.parse(event.delegateTarget.dataset.cartdata)

    const checkTermCondEl = select('#setTermCondition_applePay')
    const termCondErrorMsgEl = select('#error-setTermCondition_applePay')
    const labeltermCondition = select('label[for="setTermCondition_applePay"]')

    if (checkTermCondEl && !checkTermCondEl.checked) {
      if (!termCondErrorMsgEl && labeltermCondition) {
        const pElem = document.createElement('p')
        const errorMsg = document.createTextNode(window.config.locales.opc.customError.termsAndCondition)
        pElem.classList.add('error')
        pElem.setAttribute('id', 'error-setTermCondition_applePay')
        pElem.appendChild(errorMsg)
        labeltermCondition.appendChild(pElem)
      }
      return
    } else {
      if (termCondErrorMsgEl && labeltermCondition) {
        labeltermCondition.removeChild(termCondErrorMsgEl)
      }
    }

    this.disablePaymentModes()

    if (this.params.applePayData) {
      shippingInfo.cancelCallback = () => {
        this.enablePaymentModes()
      }
      shippingInfo.placeOrderCallback = () => {
        let formElm = select('form#sopPaymentDetailsForm')
        if (formElm) {
          formElm.action = window.config.webservices.applepay.placeOrder
          formElm.submit()
        }
      }
      shippingInfo.paymentAuthorizedFailureCallback = (response) => {
        this.enablePaymentModes()
        if (response && response.success == false) {
          if (response.stockStatusFail) {
            window.location.href = window.config.webservices.cart.cartUrl
          } else if (response.posStockStatusFail || response.deliveryOptionAvailableFail) {
            window.location.href = window.config.webservices.checkout.checkoutUrl
          } else {
            window.location.href = window.config.webservices.checkout.paymentError
          }
        }
      }

      startApplePaySession(shippingInfo, this.params.applePayData)

    }
  }

}

function initApplePayOPCManager(context = document) {
  const OPCElm = select(MODULE_SELECTOR, context)
  if (OPCElm) {
    new ApplePayOPCManager(OPCElm, 'checkout')
  }
}

export default initApplePayOPCManager
