import bind from 'delegate'
import * as xhr from 'modules/fetch'
import popin from 'modules/popin'
import { select, selectAll, getCollection } from 'lib/selectors'
import { read as readCookie, remove as removeCookie } from 'modules/cookie'
import config from 'lib/config'
import { Manager, CHECK_API } from 'lib/events'
import { encodePasswords, decodePasswords } from 'lib/helpers'

// This cookie can be created manually in your browser
// to display the popin after a page refresh
// ie. forceResetPassword=true
const COOKIE_KEY = 'forceResetPassword'
const POPIN_SELECTOR = '#js-modal-prompt-password-update'
const FORM_SELECTOR = '#js-modal-prompt-password-update-form'

export default function resetPassword() {
  let isVisible = false
  let followRedirect = true
  let userData

  // Redirect is not followed from opc
  function handleFromLogin() {
    removeCookie(COOKIE_KEY)
    showPopin(select(POPIN_SELECTOR), 'login')
  }

  function handleFromOPC(data) {
    // Let Gigya handle the login
    followRedirect = false
    // Save userData from later use
    userData = data
    // Set reset password hidden email to sign in value if necessary
    const hiddenEmailEl = document.getElementById('emailID')
    if (hiddenEmailEl.value.length === 0) {
      const sourceEl = document.getElementById('login-email')
      if (sourceEl) {
        hiddenEmailEl.value = sourceEl.value
      }
    }
    showPopin(select(POPIN_SELECTOR), 'checkout')
  }

  function showPopin(formContainerEl, page) {
    isVisible = true

    popin.open({
      label: config.locales.modal.passwordForcedUpdate.label,
      html: fixRevealPassword(formContainerEl).innerHTML,
      role: 'dialog',
      modifiers: 'is-big is-my-account',
      opener: null,
      manageFocus: false,
      forceInert: true,
      modal: true,
      closeButton: 'is-popin-close',
      closeButtonDataTest: 'btnClosePasswordUpdateRequired',
      page
    })
  }

  function checkAPI(json) {
    // 403100 Gigya status: Pending password change
    // The password is flagged and has to be updated
    if (json.errorCode === 403100) {
      // Set hidden field to previously entered email value
      const inEmailEl = document.getElementById('login-email')
      const outEmailEl = document.getElementById('emailID')
      outEmailEl.value = inEmailEl.value
      isVisible = false
      if (!isVisible) {
        handleFromLogin()
      }
    }
  }

  function handleSubmit(e) {
    function handleSuccess(data) {
      popin.hide()
      isVisible = false
      // From login: redirect to the given URL
      if (followRedirect) {
        window.location = data.redirectURL
      }
      // From OPC: validate login and move to next step
      else if (userData) {
        delete userData.forceUpdate
        Manager.publish('gigyaLogin', userData)
      }
    }

    const form = e.delegateTarget
    const selectors = ['input#password-currentPassword-popin', 'input#password-newPassword-popin']
    if (form.invalid.length) {
      return
    }

    e.preventDefault()

    // Encodes password (current and new) before sent
    encodePasswords(selectors, form)

    xhr.submit(form)
      .then(handleSuccess)
      .catch(err => {
        console.error(err)
      })

    // Restore passwords once they are sent
    decodePasswords(selectors, form)

  }


  const promptPasswordMode = readCookie(COOKIE_KEY)

  Manager.subscribe(CHECK_API, checkAPI)
  bind(document.body, FORM_SELECTOR, 'submit', handleSubmit)
  if (promptPasswordMode) {
    handleFromLogin()
  }

  return { handleFromOPC }
}

// Helpers that updates the ids so the JS bindings remains active after cloning
// ie. use of the show password relies on ids, duplicate ids messes it up
function fixRevealPassword(nodeElement) {
  const copyEl = nodeElement.cloneNode(true)
  selectAll('input[type=password]', copyEl).forEach(el => el.setAttribute('id', `${el.id}-popin`))
  selectAll('[data-toggle-password]', copyEl).forEach(el => el.setAttribute('data-toggle-password', `${el.dataset.togglePassword}-popin`))
  selectAll('label[for]', copyEl).forEach(el => el.setAttribute('for', `${el.getAttribute('for')}-popin`))
  return copyEl
}
