import bind from 'delegate'
import { select } from 'lib/selectors'

/**
 *
 * @param {*HTMLElement} scope - the element containing the data-formaction attributes
 * formaction attribute used because it is triggerd on submiting (enter key)
   and we want to change the form's action value only when input is selected
 */
export default function Toggle(scope) {
  function handleFormAction(e) {
    const el = e.delegateTarget

    if (!el.checked) {
      return
    }

    const form = el.closest('form')

    if (!form) {
      return
    }

    if (!form.defaultAction) {
      form.defaultAction = form.getAttribute('action')
    }

    form.action = el.dataset.formaction

    if (!form.action) {
      form.action = form.defaultAction
    }

    if (form.id === 'sopPaymentDetailsForm' && select('.js-remove-giftCard')) {
       form.action = window.config.webservices.checkout.giftCardPlaceOrder
     }
  }

  bind(scope, 'input[data-formaction]', 'change', handleFormAction)
}
