import * as xhr from 'modules/fetch'
import config from 'lib/config'
import bind from 'delegate'
import Loader from 'ui/widgets/loader'
import { select, getCollection } from 'lib/selectors'
import addressValidation from './address-validation'
import { Manager, CHECK_RESPONSE } from 'lib/events'
import  * as MobilePhoneValidation  from '../../config/mobile-phone-validation'
import {trimNumber} from 'lib/helpers'

const LIST_ITEMS_SELECTOR = '.js-selection-list'
const SIDEPANEL_ADD_ADDRESS_FORM_SELECTOR = '.sidepanel-edit-form form[id=addressForm]'
const DIALOG_WRONG_ADDRESS_ISCORRECT_BUTTON_SELECTOR = 'button.is-tertiary.js-popin-close'
const DIALOG_WRONG_ADDRESS_CLOSE_BUTTON_SELECTOR = 'button.is-icon.js-popin-close'

export default function List(el) {
  function displayError(data, form, sidePanel) {
    if (data.modal) {
      addressValidation.display({
        html: data.modal,
        outerClose: false,
        form,
        opener: form.querySelector('[type="submit"]'),
        // when the user decide that the filled address is valid
        validCallback: () => {
          handleEditResponse(data, sidePanel, form)
        }
      })
    } else if (data.fields){
      const payload = {
        'data': data,
        'form': form
      }
      Manager.publish(CHECK_RESPONSE, payload)
    }      
  }

  function displayMoreItems(data, selectionList) {
    if (data.html) {
      const lastItem = selectionList.children[selectionList.childElementCount - 1]

      selectionList.insertAdjacentHTML('beforeend', data.html)

      lastItem.nextElementSibling.querySelector('input').focus()
    }

    return data
  }

  function handleEditResponse(data, sidePanel) {
    // check if the item is already in the list
    let updatedItem = document.getElementById(`selection-item-${data.id}`)

    // update the item if it's in the list
    if (updatedItem) {
      const tempWrapper = document.createElement('ul')
      tempWrapper.innerHTML = data.html
      const item = tempWrapper.firstElementChild
      updatedItem.parentElement.replaceChild(item, updatedItem)
      updatedItem = item
    }
    // add the new item at the top of the list
    else {
      const selectionList = el.querySelector(LIST_ITEMS_SELECTOR)
      selectionList.insertAdjacentHTML('afterbegin', data.html)
      updatedItem = selectionList.firstElementChild
    }

    sidePanel.on('hide', () => {
      window.requestAnimationFrame(() => {
        const itemInput = updatedItem.querySelector('input')
        // preselect the item if its new or updated
        if(itemInput) {
          itemInput.click()
          itemInput.focus()
        }
      })
    })

    // close the sidepanel
    sidePanel.hide()
  }

   
  function handleSidepanelEditForm(e) {
    const form = e.target
    const sidePanel = e.delegateTarget.parentElement.modal
    const addSelector = select('.js-sidepanel-address-save') 
    if (addSelector) {
      addSelector.classList.add('is-disabled')
      addSelector.disabled = true
    }
    if (form.invalid.length) {
      addSelector.classList.remove('is-disabled')
      addSelector.disabled = false
      return
    }
    if(!form.invalid.length && config.country.toLowerCase() === "ru"){
      addSelector.classList.remove('is-disabled')
      addSelector.disabled = false
    }
    const checkboxCompanyName = select('[name=billToBusinessAddress]', form)
    const inputCompanyName = select('[name=companyName]', form)

    if (checkboxCompanyName) {
      checkboxCompanyName.value = !!checkboxCompanyName.checked
      !checkboxCompanyName.checked ? inputCompanyName.value = '' : null
    }
    e.preventDefault()
    const phoneNumber = select('.sidepanel-edit-form input[data-phone-pattern]')

    if (phoneNumber && !phoneNumber.dataset.mobileValidation) {
      MobilePhoneValidation.getUserNumber()
      .then((isValid) => {
        submitFormData(form, sidePanel)
      })
    }
    else {
      submitFormData(form, sidePanel)
    }
    
  }

  function submitFormData(form, sidePanel) {
    updateAnalyticsEditAddressSideTray()
    if (form.id === 'addressForm') {
      manageDataObj(form, sidePanel)
    }
    else {
      xhr.submit(form)
        .then(data => {
          handleEditResponse(data, sidePanel)
        })
        .catch(data => {
          displayError(data, form, sidePanel)
        })
    }
  }

  /**
 * Return new object from Form data
 */
  function manageDataObj(form, sidePanel) {
    const paramsData = {}
    const inputs = getCollection('input, select', form)
    let isoCode
    const namesToExclude = ['phoneCountryCode', 'addressId', 'countryIso', 'useDeliveryAddressEU', '_useDeliveryAddressEU', 'qualityCode', 'skipValidation', '_shippingToBusinessAddress', 'billingAddress', 'shippingAddress', 'line2', 'district', 'provincia', 'shippingToBusinessAddress', 'defaultAddress', 'municipality','village','addAddressToGigya','building']
    if((config.country.toLowerCase() === "ru") || (config.country.toLowerCase() === "cn")){
      namesToExclude.push("postcode")      
    }
    Array.from(inputs).filter(input =>
      // We add an exception for phoneCountryCode as this field is used to normalized the
      // required phoneCountryCodeField input
       (input.required || namesToExclude.indexOf(input.name) !== -1)).forEach(input => {
        if (input.type === 'radio'|| input.type === 'checkbox') {
          if (input.name === "addAddressToGigya") {
            paramsData[input.name] = input.value
          } else {
            input.checked === true ? (paramsData[input.name] = input.value) : null;
          }
        } else if (input.name === 'countryIso') {
        isoCode = input.value === 'LT' ? input.value : null
        paramsData[input.name] = input.value
      } else if((config.country.toLowerCase() === "cn") && ((input.name === "provincia") || (input.name === "district") || (input.name === "townCity"))) {
        paramsData[input.name] = input.selectedOptions[0].innerText
      } else if (input.name === 'postcode') {
        input.value.indexOf('LT') !== -1 ? input.value = input.value.replace(/[^0-9]/g, '') : null
        paramsData[input.name] = isoCode ? `${isoCode}-${input.value}` : input.value
      }
      else {
        input.value !== '' ? paramsData[input.name] = input.value : null
      }
    })
    
    if (window.config.country=='GB')
      paramsData.phone =  trimNumber(paramsData.phone)
    
    xhr.submit(form, { data: paramsData })
    .then(data => {
      handleEditResponse(data, sidePanel)
    })
    .catch(data => {
      displayError(data, form, sidePanel)
    })
  }

  function loadMoreItems(e) {
    const button = e.delegateTarget
    const selectionList = el.querySelector(LIST_ITEMS_SELECTOR)
    let replacedApi

    const loader = new Loader(button, {
      enableSyncCol: true,
      referenceEl: button
    })

    loader.init()
    loader.create()

    if (config.webservices.more[button.dataset.loadmore]) {
      replacedApi = config.webservices.more[button.dataset.loadmore]
      replacedApi = replacedApi.replace('{0}', selectionList.childElementCount)
    }

    // get the api call from the confif or directly from the button if theres no match in the config
    const api = replacedApi || button.dataset.loadmore

    xhr.get(api)
      .then(data => displayMoreItems(data, selectionList))
      .then(data => manageLoadMoreButton(data, button))
      .catch(config.noop)
      .then(loader.destroy)
  }

  function manageLoadMoreButton(data, button) {
    // update the load more button url
    if (data.next) {
      button.dataset.loadmore = data.next
    }
    // remove it if theres no more itemes to display
    else {
      button.parentElement.removeChild(button)
    }
  }
  function onUserConfirmedWrongAddress(e) {
    const formElm = select(SIDEPANEL_ADD_ADDRESS_FORM_SELECTOR, this.el)
    if(!!formElm){
      const sidePanel = formElm.offsetParent.modal

      if(formElm['skipValidation']) {
        formElm['skipValidation'].value = 'true'
      }
      submitFormData(formElm, sidePanel)
    }
  }

  function onUserCloseWrongAddress(e) {
    const addSelector = select('.js-sidepanel-address-save')
    if(!addSelector) return
    addSelector.classList.remove('is-disabled')
    addSelector.disabled = false
  }

  /**
   * Update Analytics data for Add/Edit an address for registered users in side tray
   */
  function updateAnalyticsEditAddressSideTray() {
    window.document.DLKit.push('on-tracking', {
      event: window.config.dl.event,
      eventCategory: 'edit address',
      eventAction: 'click',
      eventLabel: 'save address'
    })
  }

  bind(document.body, '.sidepanel-edit-form', 'submit', handleSidepanelEditForm)
  bind(el, '[data-loadmore]', 'click', loadMoreItems)
  bind(document.body, DIALOG_WRONG_ADDRESS_ISCORRECT_BUTTON_SELECTOR, 'click', onUserConfirmedWrongAddress)
  bind(document.body, DIALOG_WRONG_ADDRESS_CLOSE_BUTTON_SELECTOR, 'click', onUserCloseWrongAddress)
}
