  function animateStep({ step, newStep, newHeight, done }) {
    const revealNewStep = () => {
      step.el.innerHTML = newStep.innerHTML;
      Manager.publish("bindDom", step.el);

      if (!step.el.children[1]) {
        return;
      }
      TweenLite.fromTo(
        step.el.children[1],
        0.2,
        {
          opacity: 0,
        },
        {
          opacity: 1,
          onComplete: () => {
            TweenLite.set(step.el.children[1], { clearProps: "all" });
          },
        }
      );
    };

    TweenLite.fromTo(
      step.el,
      0.5,
      {
        css: {
          height: step.height,
        },
      },
      {
        css: {
          height: newHeight,
        },
        onComplete: () => {
          TweenLite.set(step.el, { clearProps: "all" });
          step.el.removeAttribute("style");
          done();
        },
      }
    );

    if (step.el.children[1]) {
      TweenLite.to(step.el.children[1], 0.2, {
        css: {
          opacity: 0,
        },
        onComplete: revealNewStep,
      });
    } else {
      revealNewStep();
    }
    zipCodeVariation();
    updateInputs();
  }

