  function displayError(data, form) {
    if (form) {
      if (Object.keys(formErrorMapping).includes(form.id)) {
        dataLayer.push({
          'eventCategory': formErrorMapping[form.id],
          'eventAction': 'error',
          'eventLabel': 'responseError_' + form.id,
          'event': 'nievent'
        })
      }
    }

    if (data.modal && form) {
      addressValidation.display({
        html: data.modal,
        modifiers: 'adress-validation',
        outerClose: false,
        form,
        opener: form.querySelector('[type="submit"]'),
        validCallback: () => {
          submitStepForm(form);
        },
      });
    } else if (data.modal && !form) {
      popin
        .open({
          label: data.label,
          html: data.modal,
          role: "dialog",
          opener: null,
          manageFocus: false,
          forceInert: true,
          outerClose: false,
          modal: true,
        })
        .then((modal) => {
          const message = modal.el.querySelector("p");
          if (message) {
            focusElement(message);
          }
        });
    } else if (!data.fields && !data.formError) {
      const input = select("[data-phone-pattern]");
      if (!isCncDeliveryMode && input.value == "") {
          MobilePhoneValidation.sampleErrorMsg(input);
      } else if (data.resultCode !== "000") {
        const payload = {
          data: { formError: window.config.locales.errors.form.global.billing.base },
          form: form,
        };
        Manager.publish(CHECK_RESPONSE, payload);
      } else {
        console.error("Something went wrong with the request", data);
      }
    } else if (data.fields) {
      const payload = {
        data: data,
        form: form,
      };
      Manager.publish(CHECK_RESPONSE, payload);
    }

    return data;
  }

