  function setFocus(container, hasFocus, inputEl) {
    if (hasFocus) {
      if (!container.classList.contains("has-focus")) {
        container.classList.add("has-focus");
      }
    } else if (container.classList.contains("has-focus") && inputEl.value === "") {
      container.classList.remove("has-focus");
    }
  }

  function managePlaceholders(elem) {
    const loginInput = elem || false;
    const passwordContainer = elem ? elem.parentElement : false;

    if (!loginInput || !passwordContainer) {
      return;
    }

    loginInput.onfocusin = function () {
      setFocus(passwordContainer, true);
    };
    loginInput.onblur = function () {
      setFocus(passwordContainer, false, loginInput);
    };
    loginInput.onchange = function () {
      const focus = loginInput.val() !== "" ? setFocus(passwordContainer, true) : setFocus(passwordContainer, false, loginInput);
    };

    const focus = loginInput.value !== "" ? setFocus(passwordContainer, true) : setFocus(passwordContainer, false, loginInput);
  }

  function manageStep1ScreenSwitch(id) {
    const step1DivElm = select(".step1", document.body);
    const gigyaWrapperElm = select(`#${GIGYA_WRAPPER_ID}`, step1DivElm);
    const guestDivElm = select("#checkout-guest-sign-in", step1DivElm);
    const gigyaLoginScreen = select("#gigya-login-screen", document.body);
    const gigyaRegisterScreen = select("#gigya-register-screen", document.body);

    // According on id, if Guest we show Guest section and hide gigya wrapper section else reverse
    if (id === "checkout-guest-sign-in") {
      hideBlock(gigyaWrapperElm);
      showBlock(guestDivElm);
      updatePageTitle(focusInteractiveStep(guestDivElm));
    } else {
      // If gestDivElm is not hidden then we need to manually switch Gigya
      const forceSwitch = !guestDivElm.hasAttribute("aria-hidden");
      hideBlock(guestDivElm);
      showBlock(gigyaWrapperElm);

      if (id === "checkout-sign-in") {
        updatePageTitle(focusInteractiveStep(gigyaLoginScreen));
      }

      if (id === "checkout-register") {
        updatePageTitle(focusInteractiveStep(gigyaRegisterScreen));
      }

      // We want to force the switch only when coming from guest page, eitherway we get an error as the switch is done twice
      if (forceSwitch) {
        window.gigya.accounts.switchScreen({
          screenSet: "chanel-one",
          containerID: GIGYA_WRAPPER_ID,
          screen: id === "checkout-sign-in" ? "gigya-login-screen" : "gigya-register-screen",
        });
      }
    }
    return;
  }

