  function hideBlock(context) {
    if (context) {
      context.classList.add(HIDDEN_CLASSNAME);
      context.setAttribute("aria-hidden", "true");

      inert.set(context);
      const fields = Array.from(context.querySelectorAll("input, textarea, select"));
      fields.forEach((field) => {
        field.setAttribute("disabled", true);
      });

      Manager.publish(REMOVE_ERROR, context);
    }
  }

  function showBlock(context) {
    if (context) {
      context.classList.remove(HIDDEN_CLASSNAME);
      context.removeAttribute("aria-hidden");

      const fields = Array.from(context.querySelectorAll("[disabled]"));
      fields.forEach((field) => {
        if (!field.closest('[aria-hidden="true"]')) {
          field.removeAttribute("disabled");
        }
      });
      inert.unset(context);
    }
  }

