  /**
   * Private fn: This method are used to update the mobiles burger menu
   * It should be used for this purposes only
   * @param res {JSON}: contains xhr.request response
   * @return {*} initial request response
   */
  function updateMobileBurgerMenu(res) {
    Manager.subscribe(PAGELOAD_WISHLIST, (response) => {
      const signInElm = selectAll(".signIn-register");
      const welcomeElm = select("[data-signin-welcome]");

      if (signInElm && welcomeElm && response.session.name) {
        // Get HTML li element (to display welcome user.firstName)
        const welcomeLiString = welcomeElm.dataset.signinWelcome;

        // Create a tmp div and extract the HTML li element
        const welcomeLiElm = document.createElement("li");
        welcomeLiElm.innerHTML = welcomeLiString;

        // Get the SVG element
        const svgElm = select("svg", welcomeLiElm);

        // update the firstName element according received values from xhr.request
        const firstNameElm = select("#signIn-welcome-firstName", welcomeLiElm);
        firstNameElm.innerHTML = `${window.config.locales.menu.welcome} ${response.session.name}`;
        firstNameElm.setAttribute("href", response.session.accountUrl);
        firstNameElm.appendChild(svgElm);

        // set current li.signIn-register by the li.signIn-Welcome elm
        if (signInElm) {
          signInElm.forEach((elm) => {
            elm.innerHTML = welcomeLiElm.childNodes[0].innerHTML;
            if (elm.id !== "signIn-register") {
              const menuNameList = elm.id.split("-");
              const firstNameElm = select("#signIn-welcome-firstName", elm);
              firstNameElm.id = `signIn-welcome-firstName-${menuNameList[menuNameList.length - 1]}`;
              firstNameElm.removeAttribute("class");
            }
          });
        }
      }
      return response;
    })
    return res;
  }

