  function selectDeliveryMethod(e) {
    deliveryCalendar = new deliveryMethodCalendar();
    var selectOpt;
    if (!(e && e.delegateTarget)) {
      selectOpt = select('input[name="deliveryOptions"]:checked');
    } else {
      selectOpt = select('input[type="radio"]', e.delegateTarget);
    }
    const PSOptionWrapper = select(".POSOptionWrapper");
    const shippingAddressSection = select(".shippingAddressSection");
    const cartPriceEle = select(".cart-product-list");
    const customerEle = select(".customer");
    const customerAsideEle = select(".customer__aside");
    const stickyMap = select(".POSMap");

    if (selectOpt) {
      selectOpt.checked = true;
      var inputs = selectAll('input[name="deliveryOptions"]')
      if (inputs) {
        var inputs_length = inputs.length;
        const cncOption = select('input[id="clickAndCollect"]')
        const homeDeliveryOption = select('input[id="homeDelivery"]')
        if (cncOption && homeDeliveryOption && inputs_length > 1) {
          addhomeDeliveryCnCAnalytics()
        }
        var i = 0;
        for (; i < inputs_length; i++) {
          if (inputs[i].checked === true) {
            inputs[i].closest("div").classList.add("selectedOpt");
            inputs[i].closest("div").classList.remove("unSelectedOpt");
            if (inputs[i].id === "clickAndCollect") {
              isCncDeliveryMode = true;
              posDataLayerUpdate("CnC", inputs[i].id);
              if (selectAll('input[name="posName"]').length > 5 && !isMobileScreen()) {
                if (customerAsideEle) {
                  customerAsideEle.classList.add("customerAsideSticky");
                  customerAsideEle.style.top = "-" + (cartPriceEle.offsetHeight + 80);
                }
                if (customerEle) {
                  customerEle.style.overflow = "initial";
                }
                if (stickyMap) {
                  stickyMap.classList.add("POSMapSticky");
                }
              }
              var selectedPosId = select('input[name="posName"]:checked');
              var lat;
              var long;
              var id;
              if (selectedPosId) {
                lat = selectedPosId.getAttribute("data-lat");
                long = selectedPosId.getAttribute("data-lng");
                id = selectedPosId.getAttribute("id");
                initializeMap(lat, long, id);
              }
              if (PSOptionWrapper) {
                PSOptionWrapper.closest("fieldset").removeAttribute("hidden");
                shippingAddressSection.setAttribute("hidden", "");
                var inputPOS = selectAll('input[name="posName"]');
                if (inputPOS) {
                  var totalPOS = inputPOS.length;
                  if (totalPOS === 1) {
                    inputPOS[0].checked = true;
                    select(".moreDetailsPOS", inputPOS[0].closest("li")).removeAttribute("hidden");
                    select(".leadTimeExpended", inputPOS[0].closest("li")).removeAttribute("hidden");
                    select(".leadTimeCollapsed", inputPOS[0].closest("li")).setAttribute("hidden", "");
                  }
                }
              }
            } else {
              isCncDeliveryMode = false;
              if (stickyMap) {
                stickyMap.classList.remove("POSMapSticky");
              }
              if (customerAsideEle) {
                customerAsideEle.removeAttribute("style");
                customerAsideEle.classList.remove("customerAsideSticky");
              }
              if (customerEle) {
                customerEle.removeAttribute("style");
              }
            }
            focusElement(inputs[i], { notChangeTabIndex: true });
          } else {
            inputs[i].closest("div:not(.notAvailableOpt)").classList.add("unSelectedOpt");
            inputs[i].closest("div").classList.remove("selectedOpt");
            if (PSOptionWrapper) {
              PSOptionWrapper.closest("fieldset").setAttribute("hidden", "");
            }
            shippingAddressSection.removeAttribute("hidden");
          }
        }
      }
    } else {
      if (config.country.toLowerCase() == "us" && !isWideScreen()) {
        let posMap = select(".POSMap");
        if (posMap) {
          posMap.setAttribute("hidden", "");
        }
      }
    }
    shippingOptionHighlight();
    sendShippingDataToDatalayer();
    const weekendDeliverySelect = select('select', document.getElementById("weekendDeliveryOptions"))
    if (weekendDeliverySelect) {
      handleChange(weekendDeliverySelect);
    }
  }
