  function initializeMap(lat, lng, posTitle) {
    var map;
    var style = [
      {
        elementType: "labels.icon",
        stylers: [
          {
            visibility: "off",
          },
        ],
      },
    ];
    var center = new google.maps.LatLng(lat, lng);
    var mapOptions = {
      center: center,
      mapTypeId: google.maps.MapTypeId.ROADMAP,
      disableDefaultUI: true,
      zoomControl: true,
      gestureHandling: "greedy",
    };
    var mobileMapElement = select("#googleMapMobile-" + posTitle);
    let posMapBlock = select(".POSMap");
    if (isMobile) {
      if (posMapBlock) posMapBlock.setAttribute("hidden", "")
      if (mobileMapElement) {
        mobileMapElement.closest(".POSMapMobile").removeAttribute("hidden");
        map = new google.maps.Map(mobileMapElement, mapOptions);
      }
    }
    else if (isTabletScreen()) {
      if (posMapBlock) posMapBlock.setAttribute("hidden", "")
      if (mobileMapElement) {
        mobileMapElement.closest(".POSMapMobile").removeAttribute("hidden");
        map = new google.maps.Map(mobileMapElement, mapOptions);
      }
    }
    else if (isWideScreen()) {
      if (mobileMapElement) {
        mobileMapElement.closest(".POSMapMobile").setAttribute("hidden", "");
      }
      if (posMapBlock) posMapBlock.removeAttribute("hidden")
      map = new google.maps.Map(document.getElementById("googleMap"), mapOptions);
    }
    var mapType = new google.maps.StyledMapType(style, { name: "Grayscale" });
    map.mapTypes.set("grey", mapType);
    map.setMapTypeId("grey");
    var latlngbounds = new google.maps.LatLngBounds();
    var latLng = { lat: Number(lat), lng: Number(lng) };
    var pt = new google.maps.LatLng(lat, lng);
    var marker_image = `${config.theme}/assets/media/pin-black.png`;
    var pinIcon = new google.maps.MarkerImage(marker_image, null, null, null, new google.maps.Size(22, 22));
    var marker = new google.maps.Marker({
      position: latLng,
      map: map,
      icon: pinIcon,
    });
    latlngbounds.extend(marker.position);
    marker.addListener("click", function () {
      selectPOSNow(posTitle);
    });
    if (posTitle) {
      map.setCenter(pt);
      map.setZoom(17);
    } else {
      if (!isMobileScreen()) {
        var bounds = new google.maps.LatLngBounds();
        map.setCenter(latlngbounds.getCenter());
        map.fitBounds(latlngbounds);
      }
    }
  }

