  function selectSavedCard(e) {
    var selectedCard = e.delegateTarget;
    if (!selectedCard) {
      selectedCard = e;
    }
    const id = selectedCard.getAttribute("id");
    if (selectedCard) {
      selectedCard.checked = true;
      var inputs = selectAll('input[name="subscriptionId"]');
      var selectedCvvId = "";
      if (inputs) {
        var inputs_length = inputs.length;
        var j = 0;
        for (; j < inputs_length; j++) {
          if (inputs[j].checked === true) {
            if (inputs[j].id == "payment-method-add") {
              var setDefaultEl = select("#setDefaultPaymentMethod");
              if (setDefaultEl) {
                setDefaultEl.removeAttribute("required");
                setDefaultEl.setAttribute("aria-required", false);
                setDefaultEl.setAttribute("disabled", true);
              }
              var setSavePaymentMethodEl = select("#savePaymentMethod");
              if (setSavePaymentMethodEl) {
                setSavePaymentMethodEl.removeAttribute("required");
                setSavePaymentMethodEl.setAttribute("aria-required", false);
              }
              if (setSavePaymentMethodEl.checked) {
                setDefaultEl.removeAttribute("disabled");
              }
              selectedCvvId = "card_cvn";
            } else {
              selectedCvvId = "card_cvn" + inputs[j].value;
            }
          }
          if (inputs[j].id !== "payment-method-add") {
            var cvvLabelElement = select("#card-cvv-block-" + inputs[j].value);
            if (inputs[j].checked === true && cvvLabelElement) {
              cvvLabelElement.removeAttribute("class");
              cvvLabelElement.removeAttribute("hidden");
              focusElement(inputs[j], { notChangeTabIndex: true });
            } else {
              if (cvvLabelElement) {
                cvvLabelElement.setAttribute("hidden", "");
                cvvLabelElement.removeAttribute("class");
              }
            }
          }
        }
      }
      selectAll(".js-card-cvn").forEach((cvnInputField) => {
        if (cvnInputField.id == selectedCvvId) {
          cvnInputField.setAttribute("name", "card_cvn");
          cvnInputField.setAttribute("data-encrypted-name", "cvc");
        } else {
          cvnInputField.setAttribute("name", "card_cvn_optional");
          cvnInputField.setAttribute("data-encrypted-name", "cvc_optional");
          cvnInputField.setAttribute("disabled", "disabled");
        }
      });
    }
  }

