  /**
   * Update Analytics data for click and collect
   * and select POS functionality
   */
  function posDataLayerUpdate(type, title) {
    const posListArray = selectAll('input[name="posName"]');
    var totalPOS;
    if (posListArray) {
      totalPOS = posListArray.length;
    }
    var dataset;
    switch (type) {
      case "CnC":
        dataset = {
          eventCategory: window.config.dl.opc.deliveryMethod.method,
          eventAction: window.config.dl.opc.deliveryMethod.cnc,
          event: window.config.dl.event,
          eventLabel: title,
          click_and_collect_number_of_store: totalPOS,
        };
        break;
      case "posMap":
        dataset = {
          eventCategory: window.config.dl.opc.deliveryMethod.method,
          eventAction: window.config.dl.opc.deliveryMethod.eventActionMap,
          event: window.config.dl.event,
          eventLabel: title,
          click_and_collect_number_of_store: totalPOS,
        };
        break;
      default:
        var selectedPosId = select('input[name="posName"]:checked');
        if (selectedPosId) title = selectedPosId.getAttribute("value");
        dataset = {
          eventCategory: window.config.dl.opc.deliveryMethod.method,
          eventAction: window.config.dl.opc.deliveryMethod.eventActionList,
          event: window.config.dl.event,
          eventLabel: title,
          click_and_collect_number_of_store: totalPOS,
        };
        break;
    }
    window.document.DLKit.push("on-tracking", dataset);
  }

