  function handleUnsubscribe(form) {
    if (form.invalid.length) {
      return;
    }
    if (select(".js-change-number")) select(".js-change-number").classList.add("disabled-btn");
    xhr
      .submit(form)
      .then((data) => {
        if (data.success) {
          if (select("#notification-subscribed")) select("#notification-subscribed").setAttribute("hidden", "");
          if (select("#notification-unsubscribed")) select("#notification-unsubscribed").removeAttribute("hidden");
          selectAll('[name="modifiedTime"]').forEach((inpt) => {
            inpt.value = data.modifiedTime;
          });
          const pElement = select("p.unsubscribed");
          if (pElement) {
            focusElement(pElement);
            if (select(".js-change-number")) select(".js-change-number").classList.remove("disabled-btn");
          }
        }
      })
      .catch((data) => {
        displayError(data, form);
      });
  }

