  /**
   * when payment mode selected BCMC then hide input security code and save for later checkbox.
   * paymentModeSelected function checks for code 'ban_contact'
   */
  function paymentModeSelected(event) {
    let paymentInfoNodes = getCollection(".ban-contact-info-eu");
    let cardNumber = select("#cardNumber");
    paymentInfoNodes.forEach((paymentInfoNode) => {
      let type = paymentInfoNode.type;
      if (event.target.value === "ban_contact") {
        paymentInfoNode.classList.add(HIDDEN_CLASSNAME);
        paymentInfoNode.setAttribute("aria-hidden", "true");
        cardNumber.setAttribute("maxlength", "19");
        if (type === "text" || type === "number") {
          paymentInfoNode.setAttribute("disabled", true);
        }
      } else {
        paymentInfoNode.classList.remove(HIDDEN_CLASSNAME);
        paymentInfoNode.removeAttribute("aria-hidden");
        cardNumber.setAttribute("maxlength", "16");
        if (type === "text" || type === "number") {
          paymentInfoNode.removeAttribute("disabled");
        }
      }
    });
  }

