  function toshiDeliveryMode(form) {
    let toshiDeliveryMehod = select(".toshi-method");
    let toshiFlag = false;
    if (toshiDeliveryMehod && toshiDeliveryMehod.children[0].checked == true) {
      const deliverySlotSelected = getCollection(".css-1odr93t");
      const inputElement = select(".toshi-method > input");
      const dataTime = getCollection(".css-1odr93t");
      if (dataTime.length > 1) {
        toshiFlag = true;
        inputElement.setCustomValidity("toshi.invalid");
        inputElement.checkValidity();
        focusElement(inputElement);
        updateAnalyticErrorToshi();
        return true;
      }
    }
    return toshiFlag;
  }

  function updateDataOnWeekendDeliverySelect(e) {
    handleDeliverySlotDateSelection(e);
    const data = JSON.parse(e.srcElement.options[e.srcElement.selectedIndex].getAttribute("data-deliverymode-info"));
    const containerDiv = select(".highlightShippingNode");
    containerDiv.setAttribute("data-test", "rdbtnShippingMethod_" + data.code);
    const selectedRadio = select('input[name="delivery_method"]', containerDiv);
    selectedRadio.value = data.code;
    selectedRadio.setAttribute("data-shippingmethod-analytics", data.code + " - " + data.price);
    selectedRadio.setAttribute("data-test", "rdbtnShippingMethod_Input_" + data.code);
  }
  
  function handleChange(e) {
    const dropdown = e.target || e
    const selectedIndex = dropdown.selectedIndex
    if (dropdown.options[selectedIndex]) {
      const el = select('#weekendDeliveryOptions')
      if (dropdown.options[selectedIndex].text.includes('')) {
        const arr = dropdown.options[selectedIndex].text.split('')
        select('span', el).innerHTML = ['<span>', arr[0], '', '<b>', arr[1], '</b>', '</span>'].join('')
      } else {
        select('span', el).innerHTML = ['<span>', dropdown.options[selectedIndex].text, '</span>'].join('')
      }
    }
  }

