  function invoiceDisplay() {
    const invoice = select('[id="set-invoice"]')
    const invoiceform = select('[id="invoice-form"]')
    const invoicePrivacy = selectAll('.js-privacy')
    const invoiceAccount = selectAll('.js-company')
    if (invoice) {
	    if (invoice.checked) {
		    invoice.value = true
		    invoice.setAttribute('checked', true)
        invoiceform.removeAttribute('hidden')
        const invoiceList = select('ul.inline-invoice-radio')
        const invoiceRadio = selectAll('.js-invoice', invoiceList)
        const profesional = select('[id="invoiceForm-professional"]')
        if (invoiceRadio) {
          invoiceRadio.forEach((element) => {
            element.addEventListener('change', invoiceTypeDisplay)
          })
        }
        if (profesional.checked == true) {
          invoiceAccount.forEach((input) => {
            input.setAttribute('required', true)
            input.setAttribute('aria-required', true)
          })
        }
        invoicePrivacy.forEach((input) => {
          input.setAttribute('required', true)
          input.setAttribute('aria-required', true)
        })
      } else {
          invoice.value = false
          invoice.removeAttribute('checked')
          invoiceform.setAttribute('hidden', '')
          invoicePrivacy.forEach((input) => {
            input.removeAttribute('required')
            input.removeAttribute('aria-required')
          })
           invoiceAccount.forEach((input) => {
            input.removeAttribute('required')
            input.removeAttribute('aria-required')
          })
      }
    }
  }
  function invoiceTypeDisplay() {
    const personal = select('[id="invoiceForm-personal"]')
    const profesional = select('[id="invoiceForm-professional"]')
    const invoiceCompany = select('.js-professional')
    const invoicePersonal = select('.js-personal')
    const invoicePrivacy = selectAll('.js-privacy')
    const invoiceAccount = selectAll('.js-company')
    invoicePrivacy.forEach((input) => {
      input.setAttribute('required', true)
      input.setAttribute('aria-required', true)
    })

    if (profesional.checked == true) {
        invoiceCompany.removeAttribute('hidden')
        personal.setAttribute('checked', false)
        invoiceAccount.forEach((input) => {
          input.setAttribute('required', true)
          input.setAttribute('aria-required', true)
        })
    } else {
        invoiceCompany.setAttribute('hidden', '')
        invoicePersonal.removeAttribute('hidden')
        profesional.setAttribute('checked', false)
        invoiceAccount.forEach((input) => {
          input.removeAttribute('required')
          input.removeAttribute('aria-required')
        })
      }
  }

