  function copyQRCode(e) {
    const button = e.delegateTarget;
    const qrCode = button.getAttribute("data-qrcode");
    navigator.clipboard.writeText(qrCode);
    if(qrCode)  {
      dataLayer.push({
        'eventCategory': 'order summary',
        'eventAction': 'copy_pix_qr_code',
        'eventLabel': '',
        'event': 'uaevent' // associated event
      });
    }
    copyNotification();
  }

  function copyNotification() {
    notifier.show({
      name: config.notifierNotify.notifierTitle,
      message: `<div class="notify-head"><div class="notify-me-heading" role="heading" aria-level="1">` + config.notifierNotify.notifierTitle + `</div>
      <p class="notify-desc">` + config.notifierNotify.notifierDesc + `</p></div>`,
      close: config.notifierNotify.close,
    })
  }

  window.addEventListener('resize', handlePOSMap);

  function handlePOSMap() {
    var lat;
    var long;
    var id;
    var geoPointDetails = select('input[name="posGeoLocation"]');
    var selectedPosId = select('input[name="posName"]:checked');
    if (geoPointDetails || selectedPosId) {
      if (geoPointDetails) {
        lat = geoPointDetails.getAttribute("data-lat");
        long = geoPointDetails.getAttribute("data-lng");
        id = geoPointDetails.getAttribute("id");
      }
      else if (selectedPosId) {
        lat = selectedPosId.getAttribute("data-lat");
        long = selectedPosId.getAttribute("data-lng");
        id = selectedPosId.getAttribute("id");
      }

      var mobileMapElement = select("#googleMapMobile-" + id);
      if (isMobile) {
        if (select(".POSMap")) {
          select(".POSMap").setAttribute("hidden", "");
        }
        if (mobileMapElement) {
          mobileMapElement.closest(".POSMapMobile").removeAttribute("hidden");
        }
      }
      else if (isTabletScreen()) {
        if (select(".POSMap")) {
          select(".POSMap").setAttribute("hidden", "");
        }
        if (mobileMapElement) {
          mobileMapElement.closest(".POSMapMobile").removeAttribute("hidden");
        }
      }
      else if (isWideScreen()) {
        if (select(".POSMap")) {
          select('.POSMap').removeAttribute("hidden")
        }
        if (mobileMapElement) {
          mobileMapElement.closest(".POSMapMobile").setAttribute("hidden", "");
        }
      }
      initializeMap(lat, long, id);
    }
  }

