import {select, selectAll} from 'lib/selectors'
import {focusElement, isWideScreen, tabIndexToZero} from 'lib/helpers'
import popin from 'modules/popin'
import * as xhr from 'modules/fetch'
import config from 'lib/config'
import bind from "delegate";

// Selectors
const DIALOG_CLOSE_BUTTON_SELECTOR = 'div.yes-no-modal button.popin__close.js-popin-close'
const WECHANT_ADYAN_PAYMENT_BUTTON_SELECTOR = ".js-wechat-adyan-payment";

let submitted = false;

class WechatPayQrAdyenPopin {
  constructor() {
    this.buttonClose = null
    this.params = {
    }
  }

   init() {
    this.buildTemplate()
    bind(document.body, WECHANT_ADYAN_PAYMENT_BUTTON_SELECTOR, "click", this.selectecWechatPayment);    
}

 createWebConfiguration = (resp) =>{
    this.webComponentConfiguration = {
        locale: resp.configuration.locale,
        environment: resp.configuration.environment,
        clientKey: resp.configuration.clientKey,
        amount: resp.configuration.amount,
        countryCode: resp.configuration.countryCode,
        onAdditionalDetails: this.submitPaymentData, 
        onSubmit: function (state, component) {
          console.log("onSubmit", state, component);
          // document.getElementById("paymentData").innerText = JSON.stringify(state.data, null, 2);
        }
      }
}

handleWechatPayinitResponse = (resp) => {
    this.createWebConfiguration(resp)
    this.webAction = resp.action
    this.open()
}

 selectecWechatPayment=(e)=>{
    e.preventDefault()
    xhr.post(config.webservices.checkout.weChatPayStart).then((resp) => this.handleWechatPayinitResponse(resp))
  }

  submitPaymentData = (state, component) => {
      var paymentDataField = select("#weChatPayAdyen-form #paymentData");
      paymentDataField.value = state.data.paymentData;
      var payloadField = select("#weChatPayAdyen-form #payload");
      payloadField.value = state.data.details.payload;
      document.getElementById("weChatPayAdyen-form").submit();
  }

  buildTemplate = () => {
    this.template =
      `<div class="popin-text text-center" id="threeDS2-popin-wrapper" data-close-label="${window.config.locales.opc.threeDS2Auth.closethreeDS2Auth}" data-test="secThreeDS2Popin">
        <div class="modal-body">
            <div id="wechatpayQR-container"></div>  
           <form method="post" id="weChatPayAdyen-form" action="${window.config.webservices.checkout.weChatPaySubmit}">
                <input type="hidden" name="PaymentData" id="paymentData"/>
                <input type="hidden" name="payload" id="payload"/>
          </form>      
        </div>
      </div>`
  }

  onOpenPopin = () => {
    const checkout = new AdyenCheckout( this.webComponentConfiguration)
    checkout.createFromAction(this.webAction).mount('#wechatpayQR-container')
    this.setDomElements()
  }

  setDomElements = () => {
    this.popinEl = select('.weChatPayQRPopin')
    if (this.popinEl) {
      this.popinEl.setAttribute('tabindex', '-1');
    }

    this.buttonClose = select('.js-popin-close', this.popinEl)
    this.form = select('form', this.popinEl)
  }

  /**
   * display a yes(remove) or no(cancel) dialog
   * @param data {Object}: contains additional params
   */
   open = () => {
    popin.open({
      html: this.template,
      role: 'dialog',
      label:  config.wechat.qrCodePopin.title,
      modifiers: 'is-relative weChatPayQRPopin',
      opener: null,
      manageFocus: true,
      forceInert: true,
      modal: true,
      action: 'removeCancelModal',
      outerClose: false
    }).then(() => {
      this.onOpenPopin()
    })
    // To catch the esc key
    window.addEventListener('keydown', this.onEscapeKeyClicked)
  }

  /**
   * Close the active modal
   */
  closeModal = () => {
    window.removeEventListener('keydown', this.onEscapeKeyClicked)
    popin.hide()
  }

  /**
   * Triggered once user clicked on the modal cancel button
   * @param params {MouseEvent}: contains MouseEvent parameters
   */
  onModalCancelButtonClicked = (e) => {
    this.closeModal()
    focusElement(this.params.clickedElm)
    if (!isWideScreen()) {
      const links = selectAll('.collapse-panel__header .js-collapse-button')
      if (links.length > 0) {
        tabIndexToZero(links)
      }
    }
  }

  /**
   * Triggered once user clicked on keyboard inputs
   * @param e {MouseEvent}: MouseEvent parameters 
   */
  onEscapeKeyClicked = (e) => {
    if ((e.key === 'Escape' || e.key === 'Esc' || e.keyCode === 27)) {
      this.onModalCancelButtonClicked(e)
    }
  }

}

const initWechatPayQrAdyenPopin = () => {
    const weChatPopin = new WechatPayQrAdyenPopin()
    weChatPopin.init()
}

export default initWechatPayQrAdyenPopin