import bind from 'delegate'
import config from 'lib/config'
import inert from 'lib/inert'
import { Manager } from 'lib/events'
import notifier from 'modules/notifier'
import { select, getCollection } from 'lib/selectors'
import { focusElement } from 'lib/helpers'

const PROMOCODE_ID = 'promocode'
const PROMOCODE_FORM_ID = 'promocode-form'
const PROMOCODE_INTRO_ID = 'promocode-intro'
const TOGGLE_SELECTOR = '.js-promocode'
const HIDDEN_CLASSNAME = 'is-hidden'
	
	
function PromocodeToggle() {
	
  function toggle(e) {
    e.preventDefault()

    const form = document.getElementById(PROMOCODE_FORM_ID)
    if (form) {
      form.classList.remove(HIDDEN_CLASSNAME)
      inert.unset(form)

      const focusable = form.querySelector('input:not([type="hidden"])')
      if (focusable) {
        focusable.focus()
      }
    }

    const intro = document.getElementById(PROMOCODE_INTRO_ID)
    if (intro) {
      intro.parentNode.removeChild(intro)
    }
    const dataLyerType = 'promo code'
    promoCodeDataLayerEntry(dataLyerType)
  }

  bind(document.body, `#${PROMOCODE_ID} ${TOGGLE_SELECTOR}`, 'click', toggle)
}
function highlightShippingNodeWithPromo(e) {
  if (e && e.delegateTarget) {
    let radioButton = select('input[name=delivery_method]', e.delegateTarget)
    if (radioButton) {
      radioButton.checked = true
    }
  }
  let shippingNodes = getCollection('.shippingItem')
  shippingNodes.forEach((shippingNode) => {
    if (shippingNode.children[0].checked !== true) {
      shippingNode.classList.remove('highlightShippingNode')
      getCollection('.shippingmethod__details', shippingNode).forEach((detailsSection) => {
        detailsSection.classList.add('is-hidden')
        getCollection('select', detailsSection).forEach((selectBox) => {
          selectBox.removeAttribute('name')
        })
      })
    }
    else {
      if (!shippingNode.classList.contains('highlightShippingNode')) {
        shippingNode.classList.add('highlightShippingNode')
        getCollection('.shippingmethod__details', shippingNode).forEach((detailsSection) => {
          detailsSection.classList.remove('is-hidden')
          getCollection('select', detailsSection).forEach((selectBox) => {
            selectBox.setAttribute('name', selectBox.dataset['name'])
          })
        })
      }
    }
  })
}
function Promocode(data) {
  const el = document.getElementById(PROMOCODE_ID)

  function set() {
    if (!el) {
      return
    }
    el.innerHTML = data.parts.promocode
    Manager.publish('bindDom', el)
   
   const formTypeRemove = el.querySelector('form[data-form-type="promocode-remove"]')
   const formTypeAdd = el.querySelector('form[data-form-type="promocode-add"]')
   
    if(formTypeRemove){
      setFocusAdd()
    }
    if(formTypeAdd){
      setFocusRemove()
   }
   highlightShippingNodeWithPromo()
  }

  function setFocusAdd() {
    let focusable = Array.from(el.querySelectorAll('.confirmation')).pop()
    if (focusable) {
      focusable = focusable.querySelector('p') || focusable
      focusable.setAttribute('tabindex', -1)
    }
    else {
      focusable = el.querySelector('input:not([type="hidden"])')
    }

    if (focusable) {
      focusable.focus()
    }
  }

  function setFocusRemove() {
    let confirmation = Array.from(el.querySelectorAll('.confirmation'))
    let focusable

    if (confirmation.length) {
      confirmation = confirmation.pop()
      focusable = confirmation.querySelector('button[type="submit"]')
    }
    else {
      focusable = el.querySelector('input:not([type="hidden"])')
    }
    if (data.message) {
      const message = data.message
      const params = {
        name: message.label || config.locales.notifier.request.label,
        message: message.text || message,
        close: message.close || config.locales.notifier.request.close,
        noFocus: !document.body.classList.contains('is-using-keyboard')
      }
      if (focusable) {
        params.opener = focusable
      }
      notifier.show(params)
    }
    else if (focusable) {
      focusable.focus()
    }

   const addNewEle =  select('#addNewValue')
   if (addNewEle) {
	   const addNewValue = addNewEle.value
	   if (addNewValue) {
        const newPromoBtn = document.getElementById(addNewValue)
         if (newPromoBtn) {
           focusElement(newPromoBtn)
        }
      }
    }
  }

  set()
}

function promoCodeDataLayerEntry(type) {
  var dataset = {
    'eventCategory': 'delivery',
    'eventAction': 'click',
    'eventLabel': type,
    'event': 'uaevent'
   }
  window.document.DLKit.push('on-tracking', dataset)
}

function handlePromoAdd() {
  const voucherCode = select('#voucher-code')
  const addNewValue = select('#addNewValue')

   if (voucherCode && addNewValue) {
     addNewValue.value = voucherCode.value
   }
  const dataLyerType = 'apply promo code'
  promoCodeDataLayerEntry(dataLyerType)
}

bind(document.body, '[data-form-type="promocode-add"]', 'click', handlePromoAdd)

export {
  Promocode,
  PromocodeToggle
}
