import { getCollection, select, closest } from "lib/selectors"
import { isMobileScreen, removeElement, focusElement} from 'lib/helpers'
import bind from 'delegate'
import config from "lib/config"
import flatpickr from "flatpickr"

(async () => {
  try {
    const { flatpickrLocale } = await import(`flatpickr/dist/l10n/${config.currentLanguage.split('_')[0]}.js`)
    console.log(`${config.currentLanguage.split('_')[0]} calendar translations loaded`)
  }
  catch(err) {
    console.log('file does not exist')
  }
})();

const COUNTRY_SELECTED = config.currentLanguage.split('_')[0]
const DATE_CONTAINER = '.js-date-container'
const INPUT_FIELD = '.js-select-date-input'
const SUBMIT_BTN = '.js-date-select-submit'

class dateSelection {
  constructor() {
	this.el = document.body		
	this.events = []
	this.dateContainer = select(DATE_CONTAINER, this.el)
	if (this.dateContainer) {
		this.dateContainer.innerHTML = ''
    }
	flatpickr.localize(flatpickr.l10ns[COUNTRY_SELECTED])
	this.bindEvents()
  }
  initializeCalendar(dateControl) {
    let startDate, endDate, calendarContainer, inputControl, defaultDate, disable
    if (isMobileScreen()) {
    	calendarContainer = select(".js-calendar-overlay-mobile", document.body)
    } else {
    	calendarContainer = select(".js-calendar-container", dateControl)
    }
  
    inputControl = select(".js-select-date-input", dateControl)
    defaultDate = inputControl.getAttribute("data-defaultDate")
    startDate = inputControl.getAttribute("data-startDate")
    endDate = inputControl.getAttribute("data-endDate")
    self = this
    this.events.push(bind(this.el, INPUT_FIELD, 'input', (event) => this.onInput(inputControl, event)))
    this.events.push(bind(this.el, INPUT_FIELD, 'keydown', (event) => this.onKeydown(event)))
    if (COUNTRY_SELECTED == 'ko') {
      disable= [
        function(date) {
            return (date.getDay() === 0);
        }
      ]
    }
    this.CalendarInstance = flatpickr(dateControl, {
      minDate: startDate,
      maxDate: endDate,
      defaultDate: defaultDate,
      clickOpens: false,
      appendTo: calendarContainer,
      allowInput:true,
      wrap: true,
      disableMobile: "true",
      ...(disable != null && disable != undefined) && {disable: disable},
      onChange: function (selectedDates, dateStr, instance) {
    	self.cleanErrors(instance.input)
		let date = selectedDates[0];
		date.setHours(12);
		select(".delivery_slot_date", dateControl).value = date.getTime();
		if (isMobileScreen()) {
			calendarContainer.setAttribute("hidden","")
		}
		document.body.classList.remove("has-modal")
      },
      onOpen: function (selectedDates, dateStr, instance) {
		  if (isMobileScreen()){
			  calendarContainer.removeAttribute("hidden")
			  removeElement(".flatpickr-close-btn",instance.calendarContainer)
	    	  let closeBtn = document.createElement("span")
	    	  closeBtn.innerHTML = `<svg class="icon is-medium is-close " viewBox="0 0 1 1" aria-hidden="true">
	              <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="${config.paths.icons}icons.svg${config.targetVersion}#close"></use>
	              </svg>`
	    		  
	    	  closeBtn.classList.add("flatpickr-close-btn")
	    	  let svg = select("svg", closeBtn)
	    	  svg.addEventListener('click', function(e){
	    		  e.preventDefault()
	    		  e.stopPropagation()
	    		  instance.close()
	    	  })
	    	  instance.calendarContainer.appendChild(closeBtn)
	    	  instance.calendarContainer.setAttribute("role","dialog")
	    	  instance.calendarContainer.setAttribute("aria-label","Calendar")
	    	  document.body.classList.add("has-modal")
		  }
      },
      onClose: function () {
    	  if (isMobileScreen()){
    		  calendarContainer.setAttribute("hidden","")
    		  document.body.classList.remove("has-modal")
    	  }
      }
    });
  } 
  bindEvents() {
    let self = this;
    this.dateControl = getCollection(".delivery-method-calendar")
    this.dateControl.forEach(function (calendar, i) {
    	self.initializeCalendar(calendar)
    });
  }
  
  checkDateRange(inputControl) {
	if(inputControl.value == "") {
		this.createErrorMessage(inputControl,window.config.dl.opc.deliveryMethod.errors.emptyDate)
		return false
	}
	let startDate = new Date(inputControl.getAttribute('data-startDate'))
	let endDate = new Date(inputControl.getAttribute('data-endDate'))
	let selectedDate = new Date(inputControl.value)
	startDate.setHours(12)
	endDate.setHours(12)
	selectedDate.setHours(12)
	
	if(this.checkValidDate(inputControl)){
		if(selectedDate.getTime() >= startDate.getTime() && selectedDate.getTime() <= endDate.getTime()){
			this.cleanErrors(inputControl)
			return selectedDate.getTime()
		} else{
			this.createErrorMessage(inputControl,window.config.dl.opc.deliveryMethod.errors.unavailableDate)
			return false
		}
	}else{
		return false
	}
  }
  
  checkValidDate(dateControl) {
    const inputValue = dateControl.value.split('-').map((v) => parseInt(v.replace(/\D/g, '')))
    if (dateControl.value.match(/^\d{4}\-\d{1,2}\-\d{1,2}$/)) {
      this.cleanErrors(dateControl)
      return true
    } else {
      this.createErrorMessage(dateControl, window.config.dl.opc.deliveryMethod.errors.inValidDate)
      return false
    }
  }
  
  createErrorMessage(domElement, message) {
    this.cleanErrors(domElement)
    const elem = document.createElement('p')
    const errorMsg = document.createTextNode(message)
    const parent = domElement.parentNode
    parent.classList.add('has-errors')
    elem.classList.add('error')
    elem.setAttribute('id', 'error-date')
    elem.dataset.errorMessage = 'true'
    elem.appendChild(errorMsg)
    elem.setAttribute('data-test', 'data-test="lblDateErrormsg"')
    parent.parentNode.appendChild(elem)
    domElement.setAttribute('aria-describedby', 'error-date')
    focusElement(domElement, { notChangeTabIndex: true, caretAtTheEnd: false })
    domElement.removeAttribute('tabindex')
    return elem
  }
  
  cleanErrors(element) {
	element = element.parentNode.parentNode
    const hasErrorClasses = getCollection('.has-errors', element)
    const errorMessages = getCollection('.error', element)
    for (let i = 0; i < hasErrorClasses.length; i++) {
      hasErrorClasses[i].classList.remove('has-errors')
    }
    for (let i = 0; i < errorMessages.length; i++) {
      errorMessages[i].parentNode.removeChild(errorMessages[i])
    }
    element.removeAttribute('aria-describedby')
  }

  onKeydown(event) {
    if (this.CalendarInstance.isOpen && (event.keyCode == 37 || event.keyCode == 38 || event.keyCode == 39 || event.keyCode == 40 || event.keyCode == 13)) {
      event.stopPropagation()
    }
  }
  onInput(dateInputSelector, event) {
	const isInputCharacter = event.inputType === 'deleteContentForward' || event.inputType === 'deleteContentBackward'
    if (!isInputCharacter && (dateInputSelector.value.match(/^\d{4}$/) || dateInputSelector.value.match(/^\d{4}\-\d{2}$/))) {
      dateInputSelector.value += '-'
    }
    const startPos = dateInputSelector.selectionStart
    let cursorPosition = startPos
    dateInputSelector.classList.toggle('is-filled', dateInputSelector.value.length > 0)
    const regex = /^[0-9-]/
    const values = dateInputSelector.value.split().map((v) => {
      if (!regex.test(dateInputSelector.value[startPos - 1])) {
        cursorPosition = startPos - 1
      }
      return v.replace(/[^0-9\-]/g, '')
    })
    dateInputSelector.value = values.join('')
    if (cursorPosition === -1) cursorPosition++
    dateInputSelector.setSelectionRange(cursorPosition, cursorPosition)
  }
}

export default dateSelection
