import Loader from 'ui/widgets/loader'
import * as xhr from 'modules/fetch'

function initToshiDetails(el) {

  const {toshiCartDataUrl, toshiLibraryUrl} = window.config.webservices

  /**
  * Triggered once form is created to get data to populate the form
  */
  function getData() {
    let data = {
      data : {
          deliveryModeCode: el.getAttribute("data-method-toshi")
        }
    }

    xhr.post(toshiCartDataUrl, data).then(data => {
      showDetails(el, data.toshi)
    })
  }

  if (!window.toshi) {
    global._babelPolyfill = false
    const apiToshi = document.createElement('script')
    apiToshi.type = 'text/javascript'
    apiToshi.id = 'apiToshi'
    apiToshi.src = toshiLibraryUrl
    apiToshi.onload = () => {
        getData()
    }
    document.body.appendChild(apiToshi)
  }
  else {
    // already loaded just fill user data and mount modal
    getData()
  }

  function showDetails(el, data) {
    
    if(window.toshi) {
      const modal = window.toshi.createBookingIntegration({
          api: {
              url: data.toshiApiUrl,
              key: data.toshiApiKey
          }
      });
      
      // This is fired by the ecommerce integration when the customer attempts to
      // proceed without selecting a timeslot.
      window.showErrorMessage = () => {
        modal.setInlineErrorMessage(
          "Please add your details before proceeding!"
        );
      };
      window.hideErrorMessage = () => {
        modal.setInlineErrorMessage(undefined)
      };
      
      modal.setFirstName(data.cartUserFirstName)
      modal.setLastName(data.cartUserLastName)
      modal.setEmail(data.cartUserEmail)
      modal.setPhone(data.cartUserPhone)
      modal.setAddress(data.cartUserAddress)

      modal.setBrandCheckoutReference(data.checkoutReference)

      modal.setProducts(data.cartEntries)

      modal.mountModalSeparately(el, document.body)   
    }
  }
}

export default initToshiDetails